
import { defineComponent, PropType } from 'vue';
import { User } from '@/specification/DTO/User';

export default defineComponent({
  name: 'UserListItem',
  props: {
    user: {
      type: Object as PropType<User>,
      required: true
    }
  },
  methods: {
    onEdit() {
      this.$emit('edit');
    },

    onRemove(e: InputEvent) {
      e.preventDefault();
      this.$emit('remove', this.user.id);
    },
  }
});
