
import { defineComponent, PropType } from 'vue';
import { User } from '@/specification/DTO/User';

export default defineComponent({
  name: 'UserSettings',
  data: () => ({
    name: '',
    username: '',
    email: ''
  }),

  props: {
    user: {
      type: Object as PropType<User>,
    }
  },

  watch: {
    user(user: User) {
      this.name = user.name ? user.name : '';
      this.username = user.username ? user.username : '';
      this.email = user.email ? user.email : '';
    }
  },

  methods: {
    onEnter(e: Event) {
      e.preventDefault();
    },

    focusOnUsername() {
      /* @ts-ignore */
      this.$refs.username.focus();
    },

    onAddUserClick(e: InputEvent) {
      e.preventDefault();
      if (!this.username) {
        this.focusOnUsername();
        return;
      }

      const newUser: User = {
        id: Math.floor(Math.random() * 1000000),
        name: this.name,
        username: this.username,
        email: this.email
      };

      this.$emit('add-new-user', newUser);

      this.clearForm();
    },

    clearForm() {
      this.name = '';
      this.username = '';
      this.email = '';
    },

    onAccept(e: InputEvent) {
      e.preventDefault();
      const newUser = Object.assign(this.user, {
        name: this.name,
        username: this.username,
        email: this.email
      });

      this.$emit('change-settings', newUser);
    },
  }
});
